
header {
    background: url('../../images/main_picture.png');
    background-size: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 900px;
    background-color: rgba(0, 0, 0, 0.5);
    font-family: 'Helvetica Neue Cyr Thin'; }

.contacts {
    display: flex;
    justify-content: space-around;
    list-style-type: none;
    padding-top: 20px; }

.contacts_img {
    margin: 0 7px 11px; }

.contacts_item-phone {
    text-align: right;
    margin-right: 30px; }

.contacts_item-adress {
     margin-left: 16px; }

.contacts_link {
    display: inline-block;
    color: #ffffff;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 0.9px; }

.contacts a {
    text-decoration: none; }


//Menu
.menu_navigations {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1366px;
    height: 110px;
    margin: 30px auto 0;
    list-style-type: none;
    box-shadow: 0 5px 4px 1px rgba(1, 1, 1, 0.15);
    background-color: #ffffff; }

.menu_logo-img {
   padding: 0 0 52px 52px; }

.menu_link {
    color: #000000;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 1.34px;
    font-family: 'Roboto Condensed Light'; }


// header section-promo

.promo_production {
    margin: 136px auto 0;
    height: 33px;
    font-size: 36px;
    color: #ffffff;
    font-weight: 400;
    letter-spacing: 4.69px;
    font-family: 'Helvetica Neue Cyr Black';
    text-align: center; }

.promo_overpay {
    margin: 37px auto 0;
    height: 33px;
    color: #ffffff;
    font-family: 'Helvetica Neue Cyr Black';
    font-size: 36px;
    font-weight: 400;
    letter-spacing: 4.69px;
    text-align: center; }

.promo_sity {
    height: 22px;
    color: #ffffff;
    font-family: 'Helvetica Neue Cyr Black';
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 2px;
    margin-top: 40px;
    text-align: center; }

.promo_line {
    width: 100px;
    height: 3px;
    background-color: #fedc00;
    margin: 0 auto;
    margin-top: 40px; }

.promo_img-punkt {
    padding-bottom: 6px;
    margin-right: 14px; }

.promo_operating-mode {
    height: 18px;
    color: #ffffff;
    font-family: 'Helvetica Neue Cyr Black';
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 1.35px;
    margin-top: 36px;
    text-align: center; }

.promo_btn {
    display: block;
    margin: 0 auto;
    width: 320px;
    height: 60px;
    background-color: #fedc00;
    color: #000000;
    font-family: 'Helvetica Neue Cyr Bold';
    font-size: 20px;
    font-weight: 40000;
    letter-spacing: 1px;
    text-align: center;
    padding-top: 15px;
    margin-top: 100px; }
















