@font-face {
    font-family: 'Helvetica Neue Cyr Black';
    src: url('../fonts/HelveticaNeueCyr-Black.eot');
    src: url('../fonts/HelveticaNeueCyr-Black.eot') format('embedded-opentype');
    src: url('../fonts/HelveticaNeueCyr-Black.woff') format('woff');
    src: url('../fonts/HelveticaNeueCyr-Black.ttf') format('truetype');
    font-weight: normal;
    font-style: normal; }

